import { projects } from "./projects";
import { RouteAwareProps } from "src/declarations";
import endeavoursClasses from "./Endeavours.module.css";
import CardList from "src/components/CardList";
import Card from "src/components/CardList/Card";
import Footer from "src/components/Footer";
import Section from "src/components/Section";

const renderedProjects = projects.map(
  ({ title, thumbnailSrc, description, href }) => (
    <Card key={title} className={endeavoursClasses["project-item-root"]}>
      <a
        target="_blank"
        title={`To ${title}`}
        href={href}
        className={endeavoursClasses["project-item"]}
        rel="noreferrer"
      >
        <h3>{title}</h3>
        <img alt={`A screenshot showing ${title}`} src={thumbnailSrc} />
        <div className={endeavoursClasses["job-data"]}>
          <p>{description}</p>
        </div>
      </a>
    </Card>
  )
);

function scrollToId(url?: string, path?: string) {
  if (!url || !path) return;

  const id = url.substring(path.length + 1);

  if (id) {
    document.getElementById(id)?.scrollIntoView();
  }
}

export default function Endeavours({ url, path }: Readonly<RouteAwareProps>) {
  return (
    <>
      <Section id="projects" ref={() => scrollToId(url, path)}>
        <h1>Endeavours</h1>
        <hr />
        <h2>Projects</h2>
        <p>
          Couple of projects I've worked in, personally. Mostly made to learn.
          Most are old (and slighly broken at this point ðŸ¤•), but there are a
          couple new ones too.
        </p>
        <CardList className={endeavoursClasses["projects-list-root"]}>
          {renderedProjects}
        </CardList>
        <h2 id="others">Other endeavours</h2>
        <CardList className={endeavoursClasses["projects-list-root"]}>
          <a
            title="To the SCAR links page"
            href="https://linktr.ee/scartracks"
            target="_blank"
            rel="noreferrer"
          >
            <Card className={endeavoursClasses["other-item"]}>
              <h3>SCAR (Music production)</h3>
              <img
                alt="The logo of my music act; SCAR"
                className={endeavoursClasses["scar-logo"]}
                src="https://vmanun.github.io/portfolio/img/scar-music-logo.png"
              />
            </Card>
          </a>
        </CardList>
      </Section>
      <Footer />
    </>
  );
}
